.mob-menu {
  width: 100vw;
  min-height: 100vh;
  z-index: 9;
  color: #fff;
  opacity: .25;
  pointer-events: none;
  background: rgba(17, 21, 24, .9);
  padding: 20px;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-100vh)scale3d(.95, .95, 1);
}

.mob-menu.is-open {
  opacity: 1;
  pointer-events: all;
  transform: translateY(0)scale3d(1, 1, 1);
}

